import * as React from "react";
import * as ReactDOM from "react-dom";
import * as BDTLL from "../../../BDTLL_common";
import { SetPermissionsStepCallbackType } from "../permissions";

enum PermissionsSettingName {
    PERSONAL_DATA = "personal_data",
    ANONYMOUS_DATA = "anonymous_data",
};

interface IPermissionsDisclosureProps {
    loadingLocation: BDTLL.UserAgreementPermissionsLoadingLocation;
    setPermissionsStepCallback: SetPermissionsStepCallbackType;
};

interface IPermissionsDisclosureState {
    personalDataSwitch: boolean;
    anonymousDataSwitch: boolean;
};

export class PermissionsDisclosure extends React.Component<IPermissionsDisclosureProps, IPermissionsDisclosureState> {
    // Page title
    title: string;

    // Personal data section
    personalDataTitle: string;
    personalDataSubtitle: string;
    personalDataDescription: string;

    // Anonymous data section
    anonymousDataTitle: string;
    anonymousDataSubtitle: string;
    anonymousDataDescription: string;

    // Privacy policy section
    agreementDescription: string;
    privacyPolicy: string;
    privacyPolicyLink: string;

    // Buttons
    agreeButtonMessage: string;
    removeButtonMessage: string;

    // Tooltip
    tooltipMessage: string;

    constructor(props: IPermissionsDisclosureProps) {
        super(props);

        const defaultLocale = BDTLL.Consts.DEFAULT_LOCALE;
        const currentLocale = BDTLL.Utils.getLanguage();

        this.title = BDTLL.Utils.get_localized_text("privacy_disclosure_title");

        this.personalDataTitle = BDTLL.Utils.get_localized_text("privacy_disclosure_personal_data_section_title");
        this.personalDataSubtitle = BDTLL.Utils.get_localized_text("privacy_disclosure_personal_data_section_subtitle");
        this.personalDataDescription = BDTLL.Utils.get_localized_text("privacy_disclosure_personal_data_section_description");

        this.anonymousDataTitle = BDTLL.Utils.get_localized_text("privacy_disclosure_anonymous_data_section_title");
        this.anonymousDataSubtitle = BDTLL.Utils.get_localized_text("privacy_disclosure_anonymous_data_section_subtitle");
        this.anonymousDataDescription = BDTLL.Utils.get_localized_text("privacy_disclosure_anonymous_data_section_description");

        this.agreementDescription = BDTLL.Utils.get_localized_text("privacy_disclosure_agreement_description");
        this.privacyPolicy = BDTLL.Utils.get_localized_text("privacy_disclosure_privacy_policy");
        this.privacyPolicyLink = BDTLL.Consts.PRIVACY_POLICY_URL.replace("<LANG>", currentLocale || defaultLocale);

        this.agreeButtonMessage = BDTLL.Utils.get_localized_text("privacy_disclosure_agree");
        this.removeButtonMessage = BDTLL.Utils.get_localized_text("privacy_disclosure_remove");

        this.tooltipMessage = BDTLL.Utils.get_localized_text("privacy_disclosure_tooltip");

        this.onMouseOver = this.onMouseOver.bind(this);
        this.onMouseOut = this.onMouseOut.bind(this);
        this.agree = this.agree.bind(this);
        this.remove = this.remove.bind(this);

        this.state = {
            personalDataSwitch: false,
            anonymousDataSwitch: true,
        };
    }

    toggleSetting(settingName: PermissionsSettingName) {
        switch (settingName) {
            case PermissionsSettingName.PERSONAL_DATA:
                this.setState({
                    personalDataSwitch: !this.state.personalDataSwitch,
                });
                break;
            case PermissionsSettingName.ANONYMOUS_DATA:
                this.setState({
                    anonymousDataSwitch: !this.state.anonymousDataSwitch,
                });
                break;
            default:
                if (BDTLL.DEBUG_MODE) {
                    console.warn(`Unknown setting = ${settingName}`);
                }
                break;
        }
    }

    onMouseOver(event: React.MouseEvent) {
        if (this.state.personalDataSwitch) {
            return;
        }

        const agreeButton = document.getElementById("BDTLL_permissions_button_agree");
        let tooltip = document.getElementById("BDTLL_permissions_tooltip");
        if (tooltip && agreeButton) {
            let agreeButtonMidPoint = agreeButton.offsetLeft + (agreeButton.offsetWidth / 2);
            let agreeButtonBottomLeft = agreeButton.offsetTop + agreeButton.offsetHeight;

            tooltip.style.width = `${agreeButton.offsetWidth}px`;

            let xTooltip = agreeButtonMidPoint - (tooltip.offsetWidth / 2);
            let yTooltip = agreeButtonBottomLeft + 10;

            tooltip.style.left = `${xTooltip}px`;
            tooltip.style.top = `${yTooltip}px`;

            tooltip.classList.remove("hidden");
            tooltip.classList.add("visible");
        }
    }

    onMouseOut() {
        let tooltip = document.getElementById("BDTLL_permissions_tooltip");
        if (tooltip) {
            tooltip.classList.remove("visible");
            tooltip.classList.add("hidden");

            tooltip.style.left = `0px`;
            tooltip.style.top = `0px`;
        }
    }

    agree() {
        const agreementStatusPD = BDTLL.Utils.permissionStatusFromLoadingLocation(
            this.props.loadingLocation
        );
        const agreementStatusAD = (
            this.state.anonymousDataSwitch
                ? BDTLL.UserAgreementPermissionsStatus.GRANTED
                : BDTLL.UserAgreementPermissionsStatus.DENIED
        );

        this.props.setPermissionsStepCallback(
            BDTLL.UserAgreementPermissionsStep.SUCCESS,
            {
                [BDTLL.LocalStorageKeys.USER_AGREEMENT_STATUS_PD]: agreementStatusPD,
                [BDTLL.LocalStorageKeys.USER_AGREEMENT_STATUS_AD]: agreementStatusAD,
            },
        );
    }

    remove() {
        this.props.setPermissionsStepCallback(
            BDTLL.UserAgreementPermissionsStep.UNINSTALL,
            {},
        );
    }

    render(): React.JSX.Element {
        const agreeButtonDisabled = this.state.personalDataSwitch ? "clickable" : "disabled";
        const personalDataSwitchClassName = this.state.personalDataSwitch ? "switch_on_logo" : "switch_off_logo";
        const anonymousDataSwitchClassName = this.state.anonymousDataSwitch ? "switch_on_logo" : "switch_off_logo";

        return (
            <div className={`BDTLL_permissions_container_${this.props.loadingLocation}`}>
                <div className={`BDTLL_permissions_container_title_${this.props.loadingLocation}`}>{this.title}</div>

                <div className={`BDTLL_permissions_container_settings_${this.props.loadingLocation}`}>

                    <div className="BDTLL_permissions_container_setting">
                        <div className="BDTLL_permissions_settings_title">
                            {this.personalDataTitle}
                        </div>

                        <div className="BDTLL_permissions_settings_subtitle">
                            <div className="logo personal_data_logo"></div>
                            <div className="BDTLL_permissions_settings_subtitle_text">
                                {this.personalDataSubtitle}
                            </div>
                            <a
                                id='switch_personal_data'
                                className="right_aligned"
                                onClick={() => {this.toggleSetting(PermissionsSettingName.PERSONAL_DATA)}}
                            >
                                <div className={`logo clickable ${personalDataSwitchClassName}`}></div>
                            </a>
                        </div>

                        <div className="BDTLL_permissions_settings_description">
                            {this.personalDataDescription}
                        </div>
                    </div>

                    <div className="BDTLL_permissions_container_setting">
                        <div className="BDTLL_permissions_settings_title">
                            {this.anonymousDataTitle}
                        </div>

                        <div className="BDTLL_permissions_settings_subtitle">
                            <div className="logo anonymous_data_logo"></div>
                            <div className="BDTLL_permissions_settings_subtitle_text">
                                {this.anonymousDataSubtitle}
                            </div>
                            <a
                                id='switch_anonymous_data'
                                className="right_aligned"
                                onClick={() => {this.toggleSetting(PermissionsSettingName.ANONYMOUS_DATA)}}
                            >
                                <div className={`logo clickable ${anonymousDataSwitchClassName}`}></div>
                            </a>
                        </div>

                        <div className="BDTLL_permissions_settings_description">
                            {this.anonymousDataDescription}
                        </div>
                    </div>
                </div>

                <div className={`BDTLL_permissions_container_privacy_policy_${this.props.loadingLocation}`}>
                    <b className={`BDTLL_permissions_privacy_policy_${this.props.loadingLocation}`}>
                        {BDTLL.ReactUtils.replaceWithJsx(
                            this.agreementDescription,
                            {
                                "<PRIVACY_POLICY_URL>": (
                                    <a
                                        id="BDTLL_permissions_hui_privacy_policy"
                                        className="BDTLL_permissions_link"
                                        href={this.privacyPolicyLink}
                                        target="_blank"
                                        key="<PRIVACY_POLICY_URL>"
                                    >
                                        {this.privacyPolicy}
                                    </a>
                                )
                            }
                        )}
                    </b>

                    <div className={`BDTLL_permissions_privacy_policy_buttons_${this.props.loadingLocation}`}>
                        <button
                            id="BDTLL_permissions_button_agree"
                            type="button"
                            disabled={!this.state.personalDataSwitch}
                            className={`BDTLL_permissions_button_agree_${this.props.loadingLocation} ${agreeButtonDisabled}`}
                            onClick={this.agree}
                            onMouseOver={this.onMouseOver}
                            onMouseOut={this.onMouseOut}
                        >
                            {this.agreeButtonMessage}
                        </button>

                        <a
                            href="#"
                            id="BDTLL_permissions_hui_button_remove"
                            className={`BDTLL_permissions_link BDTLL_permissions_hui_remove_${this.props.loadingLocation}`}
                            onClick={this.remove}
                        >
                            {this.removeButtonMessage}
                        </a>

                        <div id="BDTLL_permissions_tooltip" className="tooltip hidden">
                            <span className="tooltip_text">
                                {
                                    BDTLL.ReactUtils.replaceWithJsx(
                                        this.tooltipMessage,
                                        {
                                            "<AGREE_AND_CONTINUE>": (
                                                <span className="bold" key="<AGREE_AND_CONTINUE>">
                                                    {this.agreeButtonMessage}
                                                </span>
                                            )
                                        }
                                    )
                                }
                            </span>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
};
