import * as React from "react";
import * as ReactDOM from "react-dom";
import * as BDTLL from "../../BDTLL_common";
import { PermissionsDisclosure } from "./components/permissionsDisclosure";
import { PermissionsSuccess } from "./components/permissionsSuccess";

export type SetPermissionsStepCallbackType = (
    newPermissionsStep: BDTLL.UserAgreementPermissionsStep,
    newSettings: { [key: string]: BDTLL.UserAgreementPermissionsStatus },
) => void;

interface IPermissionsProps {
    loadingLocation: BDTLL.UserAgreementPermissionsLoadingLocation;
};

interface IPermissionsState {
    currentTheme: string;
    currentPermissionsStep: BDTLL.UserAgreementPermissionsStep;
};

export class Permissions extends React.Component<IPermissionsProps, IPermissionsState> {
    constructor(props: IPermissionsProps) {
        super(props);

        const theme = window.matchMedia(
            '(prefers-color-scheme: dark)'
        ).matches ? "dark" : "light";

        this.state = {
            currentTheme: theme,
            currentPermissionsStep: BDTLL.UserAgreementPermissionsStep.DISCLOSURE,
        };

        this.initialize();
    }

    async initialize() {
        const userAgreementStatus = (
            await browser.storage.local.get([BDTLL.LocalStorageKeys.USER_AGREEMENT_STATUS_PD])
        )[BDTLL.LocalStorageKeys.USER_AGREEMENT_STATUS_PD];

        if (!BDTLL.Utils.checkUserAgreement(userAgreementStatus)) {
            this.setState({
                currentPermissionsStep: BDTLL.UserAgreementPermissionsStep.DISCLOSURE,
            });
        } else {
            this.setState({
                currentPermissionsStep: BDTLL.UserAgreementPermissionsStep.SUCCESS,
            });
        }
    }

    componentDidMount() {
        window.matchMedia('(prefers-color-scheme: dark)').addListener(({ matches }) => {
            this.setState({
                currentTheme: matches ? 'dark' : 'light'
            });
        });
    }

    setPermissionsStep = (
        newPermissionsStep: BDTLL.UserAgreementPermissionsStep,
        newSettings: { [key: string]: BDTLL.UserAgreementPermissionsStatus },
    ) => {
        if (newPermissionsStep & BDTLL.UserAgreementPermissionsStep.UNINSTALL) {
            browser.management.uninstallSelf({
                showConfirmDialog: true,
            }).then(() => {
                browser.storage.local.set({
                    [BDTLL.LocalStorageKeys.USER_AGREEMENT_STATUS_PD]: BDTLL.UserAgreementPermissionsStatus.DENIED,
                    [BDTLL.LocalStorageKeys.USER_AGREEMENT_STATUS_AD]: BDTLL.UserAgreementPermissionsStatus.DENIED,
                }).then(() => {

                }).catch((error: Error) => {
                    if (BDTLL.DEBUG_MODE) {
                        console.warn(`Could not set keys in local storage with error: ${error}`);
                    }
                });
            }).catch((error: Error) => {
                if (BDTLL.DEBUG_MODE) {
                    console.warn(`Failed to uninstall the exetension with error: ${error}`);
                }
            });
        } else {
            browser.storage.local.set({
                [BDTLL.LocalStorageKeys.USER_AGREEMENT_STATUS_PD]: newSettings[BDTLL.LocalStorageKeys.USER_AGREEMENT_STATUS_PD],
                [BDTLL.LocalStorageKeys.USER_AGREEMENT_STATUS_AD]: newSettings[BDTLL.LocalStorageKeys.USER_AGREEMENT_STATUS_AD],
            }).then(() => {
                this.setState({
                    currentPermissionsStep: newPermissionsStep,
                });
            }).catch((error: Error) => {
                if (BDTLL.DEBUG_MODE) {
                    console.warn(`Could not set keys in local storage with error: ${error}`);
                }
            });
        }
    }

    render(): React.JSX.Element {
        return (
            <div className={`BDTLL_permissions_main_${this.props.loadingLocation}`}>
                {
                    this.props.loadingLocation === BDTLL.UserAgreementPermissionsLoadingLocation.BROWSER_TAB ?
                        <div className="BDTLL_permissions_top_logo"></div>
                    : null
                }

                {
                    this.state.currentPermissionsStep & BDTLL.UserAgreementPermissionsStep.DISCLOSURE ?
                        <PermissionsDisclosure loadingLocation={this.props.loadingLocation} setPermissionsStepCallback={this.setPermissionsStep} />
                    : (
                        this.state.currentPermissionsStep & BDTLL.UserAgreementPermissionsStep.SUCCESS ?
                            <PermissionsSuccess loadingLocation={this.props.loadingLocation} />
                        : null
                    )
                }

                <div className={`BDTLL_permissions_bottom_logo_${this.props.loadingLocation}`}>
                    {
                        this.props.loadingLocation === BDTLL.UserAgreementPermissionsLoadingLocation.BROWSER_TAB ?
                            <img id="BDTLL_permissions_bottom_logo" src="../../img/permissions_bottom_logo_tab.png"></img>
                        : (
                            <img id="BDTLL_permissions_bottom_logo" src={`../../img/permissions_bottom_logo_popup_${this.state.currentTheme}.svg`}></img>
                        )
                    }
                </div>
            </div>
        );
    }
};

window.addEventListener('DOMContentLoaded', () => {
    const windowTitleElement = document.getElementById("window_title");
    if (windowTitleElement) {
        const title: any = `${BDTLL.Consts.COMPANY_NAME} ${BDTLL.Consts.PRODUCT_NAME}`;
        ReactDOM.render(
            title,
            windowTitleElement
        );
    }

    const permissionsViewElement = document.getElementById("permissionsView");

    if (permissionsViewElement) {
        ReactDOM.render(
            <Permissions loadingLocation={BDTLL.UserAgreementPermissionsLoadingLocation.BROWSER_TAB} />,
            permissionsViewElement
        );
    }
});