import * as React from "react";
import * as ReactDOM from "react-dom";
import * as BDTLL from "../../../BDTLL_common";

interface IPermissionsSuccessProps {
    loadingLocation: BDTLL.UserAgreementPermissionsLoadingLocation;
};

interface IPermissionsSuccessState {

};

export class PermissionsSuccess extends React.Component<IPermissionsSuccessProps, IPermissionsSuccessState> {
    title: string;
    description: string;
    buttonMessage: string;

    constructor(props: IPermissionsSuccessProps) {
        super(props);

        this.title = BDTLL.Utils.get_localized_text("privacy_success_title");
        this.description = BDTLL.Utils.get_localized_text("privacy_success_description");
        this.buttonMessage = BDTLL.Utils.get_localized_text("privacy_success_reload_and_activate");

        this.continueAndReload = this.continueAndReload.bind(this);
    }

    continueAndReload() {
        browser.storage.local.set({
            [BDTLL.LocalStorageKeys.USER_AGREEMENT_STATUS_PD]: BDTLL.UserAgreementPermissionsStatus.GRANTED
        }).then(() => {
            if (this.props.loadingLocation === BDTLL.UserAgreementPermissionsLoadingLocation.BROWSER_EXTENSION_POPUP) {
                window.close();
            }
        }).catch((error: Error) => {
            if (BDTLL.DEBUG_MODE) {
                console.warn(`Could not set key in local storage with error: ${error}`);
            }
        });
    }

    render(): React.JSX.Element {
        return (
            <div className={`BDTLL_permissions_container_${this.props.loadingLocation}`}>
                <div className={`BDTLL_permissions_container_title_${this.props.loadingLocation}`}>{this.title}</div>

                <div className={`BDTLL_permissions_description_${this.props.loadingLocation}`}>
                    <div>{this.description}</div>

                    <div className={`BDTLL_permissions_privacy_policy_buttons_${this.props.loadingLocation}`}>
                        <button className={`BDTLL_permissions_button_agree_${this.props.loadingLocation} clickable`} type="button" onClick={this.continueAndReload}>
                            {this.buttonMessage}
                        </button>
                    </div>
                </div>
            </div>
        );
    }
};
